import React from "react";

import { Container, Row, Col } from "reusecore/Layout";
import SectionTitle from "reusecore/SectionTitle";
import Button from "reusecore/Button";
import VintageBox from "reusecore/VintageBox";

import imgAbout from "assets/images/hosting/about.png";
import particle1 from "assets/images/app/particle/05.png";
import particle2 from "assets/images/app/particle/06.png";

import AboutSectionWrapper from "./about.style";

const About = () => {
  return (
    <AboutSectionWrapper id="about">
      <img className="section__particle one" src={particle1} alt="img" />
      <img className="section__particle two" src={particle2} alt="img" />
      <Container fullWidthSM>
        <Row Vcenter={true}>
          <Col xs={12} sm={6}>
            <img
              className="about-thumb"
              src={imgAbout}
              alt="This is about UNI-Prime"
            />
          </Col>
          <Col xs={12} sm={6} className="about-content-wrap">
            <SectionTitle
              className="section-title"
              leftAlign={true}
              UniWidth="100%"
            >
              <h4> About us </h4>
              <h2>
                Why join with us?
              </h2>
            </SectionTitle>
            <p className="about-text text-one">
              We are a family of professional traders , consultants who can support traders who trade as a secondary income in order to achieve a passive income in the international market accomplishing your mission.
              Lets break stereotypes of 9-5 and make money anytime.
            </p>
            <p className="about-text text-two">


            </p>
            <VintageBox
              right={true}
              vintageTwo={true}
              position="relative"
            >
              <Button>Join Us</Button>
            </VintageBox>
          </Col>
        </Row>
      </Container>
    </AboutSectionWrapper>
  );
};

export default About;
