import React from "react";

import { Container, Row, Col } from "reusecore/Layout";
import Button from "reusecore/Button";
import VintageBox from "reusecore/VintageBox";

import CTASectionWrapper from "./CTASection.style";

import image1 from "assets/images/hosting/CTA/01.gif";
import Particle1 from "assets/images/hosting/vintage/01.png";

const CTAHosting = () => {
  return (
    <CTASectionWrapper>
      <Container >
        <Row Vcenter={true}>
          <Col xs={12} sm={6} lg={5}>
            <div className="CTA-content">
              <h2>
                How to grow your trading career <br /> with us ?

              </h2>
              <p>
                We provide you 100% personalised trading plan with proper risk management guidelines,
                Trading analysis and mentorship.
              </p>
              {/* <VintageBox
              right={true}
              vintageTwo={true}
              position="relative"
              >
                <Button>Learn More</Button>
              </VintageBox> */}
            </div>
          </Col>
          <Col xs={12} sm={6} lg={7}>
            <div className="CTA-img">
              <img src={image1} alt="img" />
            </div>
          </Col>
        </Row>
      </Container>
    </CTASectionWrapper>
  );
};

export default CTAHosting;
